.table {
    border: 1px solid black;
    border-spacing: 0;
    width: 100%;
    height: auto;
    overflow: scroll;
}

.thead {
    /* background-color: rgba(238, 238, 238, 0.884); */
    background-color: #02c0a0;
    margin: 0px;
    border: none;
}
.thead tr th {
    padding: 10px 5px;
    font-size: 16px;
}

.tbody {
    border: 1px solid grey;
}
.tbody tr td {
    border-right: 1px solid rgba(0, 0, 0, 0.219);
    padding: 10px 5px;
    font-size: 16px;
}

.tbody tr:nth-of-type(even) {
    /* background-color: #ccc; */
    background-color: #ededed;
}
.table [type = 'number'] {
    text-align: right;
}
