.viewContainer {
    /* border: 1px solid red; */
    width: 100px;  /*  just a trick to prevent 'div' to expand with the content 'with flex grow' */
    flex-grow: 1;
    position: relative;
    overflow: hidden;
}
.filterBar {
    /* border: 1px solid red; */
    padding: 10px;
    height: 55px;
    margin-top: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: margin .3s ease;
    border-bottom: 1px solid #7774;
    /* position: absolute; */
}
.filterIconBox {
    margin-left: auto;
}
.filterBarToggler {
    /* border: 1px solid lime; */
    display: flex;
    justify-content: center;
    background: #eee6;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
    padding: 1px;
    height: 16px;
}
.slideUpFilterBar {
    margin-top: -55px;
}
.viewEntriesBox {
    /* border: 1px solid red; */
    padding: 10px;
    width: calc(100%);
    height: calc(100% - 16px);
    overflow: hidden;
    box-shadow: inset -2px -2px 4px rgba(0, 0, 0, 0.1);
}
.ViewEntriesWithFilterBar {
    height: calc(100% - 55px - 16px);
}
.entries {
    /* border: 1px solid red; */
    width: 100%;
    height: 100%;
    overflow: auto;
}
.stickyReceived {
    width: 120px;
    position: sticky;
    right: 120;
    backface-visibility: hidden;
    background-color: black;
    color: cornsilk;
}
.stickyPaid {
    width: 120px;
    position: sticky;
    right: 0;
    backface-visibility: hidden;
    background-color: black;
    color: cornsilk;
}
.alignRight {
    text-align: right;
}