.mainContainer {
	display: flex;
	/* border: 1px solid lime; */
	height: 100vh;
	position: relative;
}
.overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, .9);
	z-index: 99;
}
.sideNavContainer {
	max-width: 250px;
	transition: all .5s ease;
	z-index: 100;
}
.show_sideNav {
	left: 0 !important;
}
@media screen and (min-width: 900px) {
  .sideNavContainer {
    display: flex;
		flex-basis: 250px;
  }
}
@media screen and (max-width: 899px) {
  .sideNavContainer {
    position: absolute;
		width: 80%;
		left: -250px;
    top: 0;
		bottom: 0;
  }
}

.subContainer {
	/* border: 2px solid red; */
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	/* width: 300px; */
}

.headerContainer {
	height: 60px;
	box-sizing: border-box;
}

.content {
	flex-grow: 1;
	/* padding: 10px; */
	display: flex;
	overflow: auto;
	/* border: 1px solid black; */
	/* background-color: rgba(1, 124, 7, 0.815); */
}



