.inputBox {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

input[type="text"],
input[type="date"],
input[type="number"],
select {
     padding: 6px;
     font-size: 16px;
     background-color: #fcfcfc77;
     border: 1px solid #ccc;
     border-radius: 4px;
     outline: none;
}

input[type="text"]:focus,
input[type="date"]:focus,
input[type="number"]:focus {
    border: 1px solid #333;
}

input:disabled:hover {
    cursor: not-allowed;
}
.errorMessage {
    color: red;
    font-size: 12px;
}