.header {
  display: flex;
  align-items: center;
  height: inherit;
  padding: 0 10px;
  background: #004b4b;
}

.sideBarToggler {
  background: transparent;
  border: none;
  margin-right: 10px;
  padding: 6px 10px;
}
.sideBarToggler:hover {
  background: linear-gradient(45deg, rgba(0, 0, 0, .1), rgba(114, 114, 114, 0.8));
}
@media screen and (min-width: 900px) {
  .sideBarToggler {
    display: none;
  }
}

.activeScreenTitle {
  font-weight: normal;
  color: #c9c9c9;
}
.info {
  margin-left: auto;
  color: #c9c9c9;
}