.sideNavBar {
  display: flex;
  flex-direction: column;
  flex-basis: 250px;
  height: 100vh;
  background: #004b4b;
}
.sideNav_Header {
  display: flex;
  /* border-bottom: 1px solid black; */
  box-shadow: inset 0px -1px 5px #0005;
  align-items: center;
  padding: 10px;
  height: 60px;
  box-sizing: border-box;
}
.brandBox {
  /* padding: 10px; */
  flex-grow: 1;
  display: flex;
  align-items: center;
}
.brandLogo {
  width: 35px;
  height: 35px;
  border: 1px solid rgba(0, 0, 0, 0.212);
  border-radius: 50%;
  margin-right: 10px;
  /* for temp. text input */
  background: linear-gradient(45deg, #004b4b11, rgba(7, 219, 235, .5));
  box-shadow: 1px 2px 4px #0004;
  display: flex;
  justify-content: center;
  align-items: center;
}
.brandName {
  color: rgb(212, 212, 212);
  font-weight: 100;
  font-size: 13px;
  word-spacing: -3px;
  font-family: monospace;
}
.btnClose {
  background: linear-gradient(45deg, rgba(0, 0, 0, .1), rgba(114, 114, 114, 0.8));
  box-shadow: 1px 1px 5px #0005;
  padding: 5px 8px;
  border: none;
  margin: 0;
}
.btnClose:hover {
  background: linear-gradient(45deg, rgba(0, 0, 0, .4), rgba(114, 114, 114, .5));
}
.btnClose:active {
  transform: scale(.95);
}


.navList, .navListNested {
  list-style: none;
}
.navListNested .btnList {
  padding-left: 25px;
}

.btnList {
  background: #005e5e8e;
  color: #c9c9c9cc;
  border-radius: 0;
  margin: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.568);
  width: 100%;
  text-align: left;
}
.btnList:hover {
  background: #17c0c081;
  color: black;
}
.btnList:active, .active {
  box-shadow: inset 0px -1px 5px #0005;
}

.btnWithIcon {
  display: flex;
  justify-content: space-between;
}
.btnIcon {
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center
}
/*
.btnIcon:hover {
  background-color: white;
} */


@media screen and (min-width: 900px) {
  .btnClose {
    display: none;
  }
}