.button {
    padding: 8px 15px;
    font-size: 16px;
    background: #cccd;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    margin-top: 10px;
    margin-bottom: 10px;
}

.button:hover {
    cursor: pointer;
    background-color: #ccc;
}

.button:focus {
    border-color: black;
}