.stickyReceived {
    min-width: 80px;
    width: 100px;
    max-width: 120px;
    position: sticky;
    right: 100;
    backface-visibility: hidden;
}
.stickyPaid {
    min-width: 80px;
    width: 100px;
    max-width: 120px;
    position: sticky;
    right: 0;
    backface-visibility: hidden;
}
.stickyHeader {
    position: sticky;
    top: 0;
    background-color: #02c0a0;
    z-index: 1;
    border-right: 1px solid #000;
}

.columnReceived {
    /* background-color: #90ee90e3; */
    background-color: #eaffeae3;
}
.columnPaid {
    background-color: #fff8f0e3;
}