.managePersonsBox {
    display: flex;
    padding: 10px;
}

.personBox {
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.title {
    margin-bottom: 20px;
}
.editPersonTogglerBtn {
    margin-top: auto;
    background-color: rgba(255, 255, 255);
    border-radius: 0;
    border-width: 0 0 1px 0;
    transition: all 0.17s ease;
}
.editPersonTogglerBtn:hover {
    background-color: rgba(245, 245, 245, 0.877);
}


.searchPersonBox {
    display: flex;
    flex-direction: column;
}


.divider {
    margin: 10px;
    border-color: #ddd5;
}